/* 헤더스타일 */


.header {
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    background-color: #fff;
    box-shadow:2px 0 3px #000;
    z-index: 500;
    transition: all 500ms;
    &--hide {
        transform:translateY(-100%);
    }
    
    &__top {
        &-menu {
            height:30px;
            text-align: right;;
            font-size:0;
            &__item {
                display: inline-block;
                height:100%;
                margin:0 12px;
                text-align: center;
                &:before {
                    display: inline-block;
                    width: 1px;
                    height:100%;
                    margin-left: -1px;
                    vertical-align: middle;
                    content: '';;
                }
            }
            
            &__anchor {
                display: inline-block;
                color:#333;
                font-size:12px;
                vertical-align: middle;
            }
        }
    }
    
    &-menu {
        &-button {
            position: fixed;
            top:30px;
            right:30px;
            width:30px;
            height:25px;
            font-size:0;
        }
    }
}

.hamburger {
    position: relative;
    display: inline-block;
    width:100%;
    height:100%;
    font-size:0;
    
    
    &-menu {
        &__top,
        &__center,
        &__bottom {
            position: absolute;
            right:0;
            display: inline-block;
            height:3px;
            background-color:#666;
            transition:all 300ms;
        }
        
        &__top {
            top:0;
            width: 77%;
        }
        
        &__center {
            top:50%;
            transform:translateY(-50%);
            width:42%;
        }
        
        &__bottom {
            bottom:0;
            width:100%;
        }
    }
    
    &:hover &-menu__top {
        width: 100%;
    }
}

